.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0px 15px;
  @media screen and(min-width:480px) {
    width: 480px;
  }
  @media screen and(min-width:768px) {
    width: 768px;
  }
  @media screen and(min-width:1200px) {
    width: 1200px;
  }
}
.container.warning {
  display: flex;
  justify-content: center;
  align-items: center;
}
